import { useInviteCodes } from '@cetus/hooks'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useNavigate } from 'react-router-dom'
import { HeaderProps } from '../type'
import H5Header from './H5Header'
import PCHeader from './PCHeader'

export default function Header({ children, ...rest }: HeaderProps) {
  const { isApp } = useWindowWidth()
  const navigate = useNavigate()
  const { hasAccessPermission } = useInviteCodes()

  return isApp ? (
    hasAccessPermission ? (
      <H5Header {...rest} jump={navigate}>
        {children}
      </H5Header>
    ) : (
      <></>
    )
  ) : (
    <PCHeader {...rest} jump={navigate}>
      {children}
    </PCHeader>
  )
}
